<template>
  <div class="all">
    <div class="world">
      <div class="world-head">
        <ul class="header">
          <li><a id="one" href="#">国内疫情</a></li>
          <li><a id="two" href="#foreign">国外疫情</a></li>
        </ul>
      </div>
      <div class="middle-box">
        <div class="China-box">
          <h4 class="first-title">国内疫情</h4>
          <p class="first-date" v-if="datalist">
            数据更新至 {{ datalist.lastUpdateTime }}
          </p>
          <div class="China-numSpan" v-if="cnumlist" >
            <ul>
              <li id="count-one">
                <h4>现有确诊</h4>
                <span class="count">{{
                  cnumlist.total.confirm -
                  cnumlist.total.heal -
                  cnumlist.total.dead
                }}</span>
                <p>昨日</p>
                <span>+{{ cnumlist.today.confirm }}</span>
              </li>
              <li id="count-2">
                <h4>无症状感染者</h4>
                <span class="count">{{ cnumlist.extData.noSymptom }}</span>
                <p>昨日</p>
                <span>+{{ cnumlist.extData.incrNoSymptom }}</span>
              </li>
              <li id="count-3">
                <h4>现有疑似</h4>
                <span class="count">{{ seemlike }}</span>
                <p>昨日</p>
                <span>+{{ incSeemlike }}</span>
              </li>
              <li id="count-4">
                <h4>现有重症</h4>
                <span class="count">0</span>
                <p>昨日</p>
                <span>+0</span>
              </li>
              <li id="count-5">
                <h4>累计确诊</h4>
                <span class="count">{{ cnumlist.total.confirm }}</span>
                <p>昨日</p>
                <span>+{{ cnumlist.today.confirm }}</span>
              </li>
              <li id="count-6">
                <h4>境外输入</h4>
                <span class="count">{{ cnumlist.total.input }}</span>
                <p>昨日</p>
                <span>+{{ cnumlist.today.input }}</span>
              </li>
              <li id="count-7">
                <h4>累计治愈</h4>
                <span class="count">{{ cnumlist.total.heal }}</span>
                <p>昨日</p>
                <span>+{{ cnumlist.today.heal }}</span>
              </li>
              <li id="count-8">
                <h4>累计死亡</h4>
                <span class="count">{{ cnumlist.total.dead }}</span>
                <p>昨日</p>
                <span>+{{ cnumlist.today.dead }}</span>
              </li>
            </ul>
          </div>
          <!--折线图-->
          <div class="hello">
            <div id="main" style="width: 800px; height: 400px"></div>
          </div>
          <!--折线图结束-->
          <div style="width: 100%; padding-bottom: 30px">
            <p class="tableTitle">国内各地统计汇总</p>
            <!-- 表格挂载的对象 -->
            <el-table
              :data="chinaList"
              height="400"
              stripe
              border
              style="width: 100%"
              v-if="chinaList"
            >
              <el-table-column prop="name" label="地区"> </el-table-column>
              <el-table-column prop="today.confirm" label="新增"> </el-table-column>
              <el-table-column prop="total.new" label="现有"> </el-table-column>
              <el-table-column prop="total.confirm" label="累计"> </el-table-column>
              <el-table-column prop="total.heal" label="治愈"> </el-table-column>
              <el-table-column prop="total.dead" label="死亡"> </el-table-column>
            </el-table>
          </div>
        </div>
        <div class="bgimg"></div>
        <div class="big-title">
          <h1>
            众志成城 科学应对<br />
            群防群控 战胜疫情
          </h1>
        </div>
        <div class="Foreigns-box">
          <a id="foreign"><h4 class="first-title">国外疫情</h4></a>
          <p class="first-date" v-if="datalist">
            数据更新至 {{ datalist.lastUpdateTime }}
          </p>
          <div class="Foreigns-numSpan">
            <ul>
              <li id="count-fr1">
                <h4>现有确诊</h4>
                <span class="count">26697696</span>
                <p>昨日</p>
                <span>+135745</span>
              </li>
              <li id="count-fr2">
                <h4>累计治愈</h4>
                <span class="count">102771625</span>
                <p>昨日</p>
                <span>+386899</span>
              </li>
              <li id="count-fr3">
                <h4>累计死亡</h4>
                <span class="count">2869713</span>
                <p>昨日</p>
                <span>+6481</span>
              </li>
              <li id="count-fr4">
                <h4>累计确诊</h4>
                <span class="count">132339034</span>
                <p>昨日</p>
                <span>+529125</span>
              </li>
              <li id="count-fr5">
                <h4>治愈率</h4>
                <span class="count persentCount">77.7</span><span>%</span>
              </li>
              <li id="count-fr6">
                <h4>病死率</h4>
                <span class="count">2.2</span><span>%</span>
              </li>
            </ul>
          </div>
          <div id="main2" style="width: 800px; height: 600px"></div>
          <div style="width: 100%; padding: 30px 0">
            <p class="tableTitle">国外各地统计汇总</p>
            <!-- 表格挂载的对象 -->
            <el-table
              v-if="foreignList"
              :data="foreignList"
              border
              style="width: 100%"
              height="500"
            >
              <el-table-column prop="name" label="地区" width="180">
              </el-table-column>
              <el-table-column prop="today.confirm" label="新增"> </el-table-column>
              <el-table-column prop="total.confirm" label="累计"> </el-table-column>
              <el-table-column prop="total.heal" label="治愈"> </el-table-column>
              <el-table-column prop="total.dead" label="死亡"> </el-table-column>
            </el-table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import echarts from 'echarts';
import * as echarts from "echarts";
import data from "../../db.json";
// import axios from "axios";
// import axiosForeign from "axios";

export default {
  name: "WordData",
  data() {
    return {
      foreignList:[],
      chinaList:[],
      incSeemlike: 13,
      seemlike: 138,
      cnumlist: null,
      fnumlist: null,
      datalist: null,
      show: false,
      ChinaData: null,
      ForeignData: null,
      formLabelWidth: "120px",
      centerDialogVisible: false,
      visible: false,
      dialogTableVisible: false,
      dialogFormVisible: false,
      dbjson: null,
      form: {
        地区: "",
        新增: "",
        现有: "",
        累计: "",
        治愈: "",
        死亡: "",
      },
    };
  },

  created() {
    console.log(111);
    this.axios
      .get(
        "https://api.kele8.cn/agent?url=https://c.m.163.com/ug/api/wuhan/app/data/list-total"
      )
      .then((res) => {
        this.datalist = res.data.data;
        this.cnumlist = this.datalist.chinaTotal;
        this.chinaList = this.datalist.areaTree[2].children
        this.chinaListMap(this.chinaList)
        this.foreignList = this.datalist.areaTree
        this.foreignList.splice(2,1)
      });


      const count = document.querySelectorAll(".count");
      countUp(count);
      function countUp(domList, totalTime = 2, delay = 10) {
      Array.prototype.forEach.call(domList, (dom) => {
        const count = +dom.innerHTML;
        let initial = 0;
        const increment = Math.ceil((count / totalTime / 1000) * delay);
        const timer = setInterval(() => {
          if (initial <= count) {
            dom.innerHTML = initial += increment;
          } else {
            dom.innerHTML = count;
            clearInterval(timer);
          }
        }, delay);
      });
    }
  },
  methods: {
    chinaListMap(list){
      for( let i = 0; i < list.length; i++){
        list[i].total.new = list[i].total.confirm - list[i].total.heal
      }
    }
  },
  mounted() {
    this.dbjson = data;
    var chartDom = document.getElementById("main");
    var myChart = echarts.init(chartDom);
    var option;

    var colors = ["#5470C6", "#EE6666"];

    option = {
      color: colors,

      tooltip: {
        trigger: "none",
        axisPointer: {
          type: "cross",
        },
      },
      legend: {
        data: ["新增境外输入", "总新增确诊"],
      },
      grid: {
        top: 70,
        bottom: 50,
      },
      xAxis: [
        {
          type: "category",
          axisTick: {
            alignWithLabel: true,
          },
          axisLine: {
            onZero: false,
            lineStyle: {
              color: colors[1],
            },
          },
          axisPointer: {
            label: {
              formatter: function (params) {
                return (
                  "总新增确诊人数   " +
                  params.value +
                  (params.seriesData.length
                    ? "：" + params.seriesData[0].data
                    : "")
                );
              },
            },
          },
          data: [
            "2021-10-31",
            "2021-11-01",
            "2021-11-02",
            "2021-11-03",
            "2021-11-04",
            "2021-11-05",
            "2021-11-06",
            "2021-11-07",
            "2021-11-08",
            "2021-11-09",
            "2021-11-10",
            "2021-11-11",
            "2021-11-12",
          ],
        },
        {
          type: "category",
          axisTick: {
            alignWithLabel: true,
          },
          axisLine: {
            onZero: false,
            lineStyle: {
              color: colors[0],
            },
          },
          axisPointer: {
            label: {
              formatter: function (params) {
                return (
                  "境外输入人数  " +
                  params.value +
                  (params.seriesData.length
                    ? "：" + params.seriesData[0].data
                    : "")
                );
              },
            },
          },
          data: [
            "2021-10-31",
            "2021-11-01",
            "2021-11-02",
            "2021-11-03",
            "2021-11-04",
            "2021-11-05",
            "2021-11-06",
            "2021-11-07",
            "2021-11-08",
            "2021-11-09",
            "2021-11-10",
            "2021-11-11",
            "2021-11-12",
          ],
        },
      ],
      yAxis: [
        {
          type: "value",
        },
      ],
      series: [
        {
          name: "新增境外输入",
          type: "line",
          xAxisIndex: 1,
          smooth: true,
          emphasis: {
            focus: "series",
          },
          data: [16, 2, 5, 9, 6, 8, 7, 17, 18, 7, 8, 9, 3],
        },
        {
          name: "总新增确诊",
          type: "line",
          smooth: true,
          emphasis: {
            focus: "series",
          },
          data: [27, 27, 18, 15, 26, 13, 11, 28, 33, 22, 17, 20, 12],
        },
      ],
    };

    option && myChart.setOption(option);

    var chartDom2 = document.getElementById("main2");
    var myChart2 = echarts.init(chartDom2);
    var option2;

    setTimeout(function () {
      option2 = {
        legend: {},
        tooltip: {
          trigger: "axis",
          showContent: false,
        },
        dataset: {
          source: [
            [
              "product",
              "2021-05",
              "2021-06",
              "2021-07",
              "2021-08",
              "2021-09",
              "2021-10",
              "2021-11",
            ],
            [
              "国外现有确诊",
              23099302,
              18470715,
              22962589,
              27762184,
              26152099,
              22453512,
              22932126,
            ],
            [
              "国外新增确诊",
              440782,
              599300,
              676246,
              319998,
              619460,
              857900,
              605032,
              847902,
            ],
            [
              "国外新增治愈",
              13816672,
              19103869,
              33484303,
              17334600,
              10687272,
              15119264,
              29082612,
            ],
            [
              "国外累计死亡",
              120956,
              1499320,
              1860285,
              2540025,
              2650214,
              2872460,
              3332334,
            ],
          ],
        },
        xAxis: { type: "category" },
        yAxis: { gridIndex: 0 },
        grid: { top: "55%" },
        series: [
          {
            type: "line",
            smooth: true,
            seriesLayoutBy: "row",
            emphasis: { focus: "series" },
          },
          {
            type: "line",
            smooth: true,
            seriesLayoutBy: "row",
            emphasis: { focus: "series" },
          },
          {
            type: "line",
            smooth: true,
            seriesLayoutBy: "row",
            emphasis: { focus: "series" },
          },
          {
            type: "line",
            smooth: true,
            seriesLayoutBy: "row",
            emphasis: { focus: "series" },
          },
          {
            type: "pie",
            id: "pie",
            radius: "30%",
            center: ["50%", "25%"],
            emphasis: { focus: "data" },
            label: {
              formatter: "{b}: {@2012} ({d}%)",
            },
            encode: {
              itemName: "product",
              value: "2012",
              tooltip: "2012",
            },
          },
        ],
      };

      myChart2.on("updateAxisPointer", function (event) {
        var xAxisInfo = event.axesInfo[0];
        if (xAxisInfo) {
          var dimension = xAxisInfo.value + 1;
          myChart2.setOption({
            series: {
              id: "pie",
              label: {
                formatter: "{b}: {@[" + dimension + "]} ({d}%)",
              },
              encode: {
                value: dimension,
                tooltip: dimension,
              },
            },
          });
        }
      });

      myChart2.setOption(option2);
    });

    option2 && myChart.setOption(option2);
  },
};
</script>

<style scoped>

.all {
  margin-top: 100px;
}

.header-area {
  font-weight: 700;
}
.big-title {
  width: 760px;
  height: 300px;
  text-align: center;
  line-height: 300px;
  background: url("~@/assets/images/hmj口罩与防护镜.jpg") no-repeat;
  background-size: 1000px 700px;
  background-attachment: fixed;
  background-position: center;
}
.big-title h1 {
  padding-top: 50px;
  height: 100%;
  color: #fff;
  background: rgba(0, 0, 0, 0.2);
}
.Foreigns-numSpan {
  margin-bottom: 80px;
}
.change-btn {
  box-sizing: border-box;
  margin: 50px 200px;
  width: 800px;
  font-size: 16px;
  font-weight: 700;
}

.lab-btn {
  box-sizing: border-box;
  margin: 5px 30px;
}
</style>
