<template>
  <div class="question">
    <div class="table">
      <el-table
        v-if="wordRes"
        :data="wordRes"
        border
        style="width: 90%"
        height="700"
      >
        <el-table-column prop="result" label="类别" width="100">
        </el-table-column>
        <el-table-column prop="title" label="事件" width="630">
        </el-table-column>
        <el-table-column label="事件" width="250">
          <template #default="{ row }">
            <el-button
              @click="drawers(row)"
              type="primary"
              style="margin-left: 16px">查看详情
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-drawer
      title="全球疫情资讯网"
      :visible.sync="drawer"
      :with-header="false"
    >
      <div class="drawer-more">
        <p>
          {{ title }}<br /><br />
          {{ desc }}
        </p>
        <div class="pic">
            <img :src="imgsrc" alt="">
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import data from "../../db.json";

export default {
  props: {
    keywords: String,

  },
  data() {
    return {
      dbjson: null,
      wordRes: null,
      drawer: false,
      title: null,
      desc: null,
      imgsrc:null,
      search1: true,
      QTDataAll: null,
      form: {
        result: "",
        title: "",
      },
      form2: {
        advice: "",
      },
    };
  },
  created() {
      this.axios
        // .get(
        //   "https://api.kele8.cn/agent?url=http://api.tianapi.com/rumour/index?key=fc79965139df54549b8745d9b5d6648d&num=20"
        // )
        .then((res) => {
        //   this.wordRes = res.data.data;
          this.wordRes = res.data.newslist
          this.resMap(this.wordRes)
          console.log(this.wordRes);
        }); 
  },
  methods: {
      backAll(){
          this.wordRes = this.QTDataAll
      },
    drawers(row) {
      this.drawer = true;
      console.log(row.id);
      console.log(this.wordRes[1].title);
      this.title = this.wordRes[row.id].title;
      this.desc = this.wordRes[row.id].desc;
      this.imgsrc = this.wordRes[row.id].imgsrc;
    },
    resMap(list){
        for(let i = 0; i < list.length; i++){
            list[i].id = i
        }
        return list
    },
    search(e) {
        console.log(e);
      this.axios
        .get(
          `https://api.kele8.cn/agent?url=http://api.tianapi.com/rumour/index?key=fc79965139df54549b8745d9b5d6648d&word=${this.keywords}`
        )
        .then((res) => {
          console.log(res);
        //   this.wordRes = res.data.data;
          this.wordRes = res.data.newslist
          this.resMap(this.wordRes)
        });
        
    },
  },
  mounted() {
    this.dbjson = data;
    this.QTDataAll = this.wordRes;
  },
};
</script>

