<template>
  <header class="header-section transparent-header">
    <div class="header-area">
      <div class="container">
        <div class="primary-menu">
          <div class="logo">
            <router-link to="/">
              <img
                src="~@/assets/images/logo/bird鸽子蓝色改改.png"
                alt="logo"
                class="bird-logo"
            /></router-link>
          </div>
          <div class="main-area">
            <div class="main-menu">
              <ul class="agri-ul">
                <li
                  :class="{ active: currentIndex === 0 }"
                  @click="setIndex(0)"
                >
                  <router-link to="/home">首页</router-link>
                </li>
                <li
                  :class="{ active: currentIndex === 1 }"
                  @click="setIndex(1)"
                >
                  <router-link to="/news">
                    新闻资讯
                    <i class="el-icon-arrow-down"></i>
                  </router-link>
                  <ul class="agri-ul">
                    <li><router-link to="/news">新闻资讯</router-link></li>
                    <li><router-link to="#">404 Page</router-link></li>
                  </ul>
                </li>
                <li
                  :class="{ active: currentIndex === 2 }"
                  @click="setIndex(2)"
                >
                  <router-link to="/worldData"
                    >全球疫情数据
                    <i class="el-icon-arrow-down"></i>
                  </router-link>
                  <ul class="agri-ul">
                    <li @click="openFullScreen2">
                      <router-link to="/worldData">国内疫情数据</router-link>
                    </li>
                    <li @click="openFullScreen2">
                      <router-link to="/worldData">国外疫情数据</router-link>
                    </li>
                  </ul>
                </li>
                <li
                  :class="{ active: currentIndex === 3 }"
                  @click="setIndex(3)"
                >
                  <router-link to="/question" 
                    >辟谣答疑
                    <i class="el-icon-arrow-down" @click="openFullScreen2"></i>
                  </router-link>
                  <ul>
                    <li >
                      <router-link to="/question">立即搜索</router-link>
                    </li>
                    <li><router-link to="/question">留言提交</router-link></li>
                  </ul>
                </li>
                <li
                  :class="{ active: currentIndex === 4 }"
                  @click="setIndex(4)"
                >
                  <router-link to="/notice"
                    >学校通知
                    <i class="el-icon-arrow-down"></i>
                  </router-link>
                  <ul class="agri-ul">
                    <li><router-link to="/notice">通知查看</router-link></li>
                    <li @click="openFullScreen2">
                      <router-link to="/schoolLogin"
                        >校方管理登录入口</router-link
                      >
                    </li>
                  </ul>
                </li>
                <li @click="open">
                  <router-link to="#bottom-email0">联系我们</router-link>
                </li>
              </ul>
            </div>
            <div class="header-btn">
              <router-link to="/" class="lab-btn"
                ><span>全球疫情资讯网</span></router-link
              >
            </div>
          </div>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name: "PageNav",
  data() {
    return {
      currentIndex: 1,
      fullscreenLoading: false,
      search1:true
    };
  },
  methods: {
    openFullScreen1() {
      this.fullscreenLoading = true;
      setTimeout(() => {
        this.fullscreenLoading = false;
      }, 2000);
    },
    openFullScreen2() {
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
      });
      setTimeout(() => {
        loading.close();
      }, 2000);
    },

    setIndex(index) {
      this.currentIndex = index;
    },
    open() {
      this.$alert("291459044@qq.com", "欢迎致信：", {
        confirmButtonText: "本同学已阅",
        callback: (action) => {
          console.log(action);
          this.$message({
            type: "info",
            message: `action: ${"感谢您的来信，我们会尽快回复"}`,
          });
        },
      });
    },
  },
};
</script>

